.orders-tools-block {
  display: flex;
  justify-content: space-between;
  // border: 1px solid rgba(128, 128, 128, 0.153);
  margin-top: 6px;
  padding: 4px;
  border-radius: 4px;
  margin-bottom: 4px; 
  div {
    display: flex;
    flex-wrap: wrap;
    max-width: 1900px;
    font-size: inherit;
  }
  .line-block {
    box-shadow: 0px 0px 0px 2px rgba(128, 128, 128, 0.07);
    // box-shadow: 0px 0px 0px 2px rgba(150, 171, 145, 0.57);
    margin: 4px;
    padding: 4px;
    display: flex;
    flex-direction: column; 
    justify-content: center; 
    // border: 1px solid darkseagreen;
    &:hover {
      box-shadow: 1px 1px 1px 1px rgba(128, 128, 128, 0);
    }
    select {
      min-width: 76px;
      // border: 1px solid #f5ead9;
      padding: 4px;
    }
    input {
      font-size: initial;
      font-style: normal;
      // padding: 1px;
      text-align: center;
      border: 1px solid #898585;
      background-color: #faebd759;
    }
    
  }
  label {
    display: flex;
    margin: 0.5rem;
    align-items: center;
  }
}