

#header {
  display: flex;
  flex-wrap: wrap;
  // max-height: 40px;
  background-color: #e2e0a8;
  align-items: center;
  .userOrg {
    margin: 4px 8px;
    overflow: hidden;
    max-height: 30px;
    p {
      padding: 0px;
      margin: 0px 8px;
    }
  }
  img {
    border-style: none;
    border-width: 0;
    width: 239px;
    aspect-ratio: auto 239 / 30;
    height: 30px;
    margin: 4px;
  }
  .header-menu {
    display: flex;
    a {
      // padding: 10px 6px; 
      text-decoration: none;
      outline: none;
      display: block;
      transition: .3s ease-in-out;
      cursor: pointer;
    }
  }
}

.title {
  font-size: 20px;
  text-align: center;
  margin: 0 auto;
  padding: 12px;

}

nav {
  display: table;
  margin: 0 auto;
}
nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.topmenu {
  display: flex;
}
.topmenu:after {
  content: "";
  display: table;
  clear: both;
}
.topmenu > li {
  // width: 25%;
  float: left;
  position: relative;
  font-family: 'Open Sans', sans-serif;
}
.topmenu > li > a {
  text-transform: uppercase;
  // font-size: 14px;
  // font-weight: bold;
  color: #404040;
  padding: 5px 10px;
}
.topmenu li a:hover { color: #D5B45B; }
.submenu-link:after {
  content: ">";
  // content: "\f107";
  font-family: "Roboto";
  color: inherit;
  font-size: small;
  margin-left: 2px;
  display: inline-block;
  transform: rotate(90deg);
}
.submenu {
  background: #273037;
  position: absolute;
  left: 0;
  top: 100%;
  z-index: 5;
  width: 180px;
  opacity: 0;
  transform: scaleY(0);
  transform-origin: 0 0;
  transition: .5s ease-in-out;
}
.submenu a {
  color: white;
  text-align: left;
  padding: 12px 15px;
  font-size: 13px;
  border-bottom: 1px solid rgba(255,255,255,.1);
}
.submenu li:last-child a { border-bottom: none; }
.topmenu > li:hover .submenu {
  opacity: 1;
  transform: scaleY(1);
}