.tab-block {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  // border: 1px solid rgba(48, 46, 46, 0.153);
  margin-top: 4px;
  border-radius: 4px;
  padding: 0px 0;
  margin-top: 0px; 
  .tcnt-order {
    margin-top: 15px;
    display: table;
    border-collapse: collapse;
    box-sizing: border-box;
    text-indent: initial;
    border-spacing: 0px;
    border-color: grey;
    word-wrap: break-word; /* Перенос слов */ 
    width: -webkit-fill-available;
    .odds {
      background-color: #dfdfdf;
    }
    .row-block__body {
      input[type=checkbox] {
        visibility: hidden;
      }
      a {
        color: #660000;
      }
       
    
    }
    .squaredTwo {
      width: 28px;
      height: 28px;
      background-color: #dddcad38;

      // background: -webkit-linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
      // background: -moz-linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
      // background: -o-linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
      // background: -ms-linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
      // background: linear-gradient(top, #fcfff4 0%, #dfe5d7 40%, #b3bead 100%);
      // filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fcfff4', endColorstr='#b3bead',GradientType=0 );
      // margin: 20px auto;

      -webkit-box-shadow: inset 0px 1px 1px white, 0px 1px 3px rgba(0,0,0,0.5);
      -moz-box-shadow: inset 0px 1px 1px white, 0px 1px 3px rgba(0,0,0,0.5);
      box-shadow: inset 0px 1px 1px white, 0px 1px 3px rgba(0,0,0,0.5);
      position: relative;
      transition: all .5s;
      .point-checked {
        z-index: 3;
        width: 2px;
        height: 2px;
        position: absolute;
        top: calc(50% - 9px);
        left: calc(50% - 6px);
        color: black;
      }

      label {
        cursor: pointer;
        position: absolute;
        width: 20px;
        height: 20px;
        left: calc(50% - 12px);
        top: calc(50% - 10px);
        transform: scale(1.0);

        -webkit-box-shadow: inset 0px 1px 1px rgba(0,0,0,0.5), 0px 1px 0px rgba(255,255,255,1);
        -moz-box-shadow: inset 0px 1px 1px rgba(0,0,0,0.5), 0px 1px 0px rgba(255,255,255,1);
        box-shadow: inset 0px 1px 1px rgba(0,0,0,0.5), 0px 1px 0px rgba(255,255,255,1);
  
         
        background: linear-gradient(to top, #a5a5a5 0%, #45484d 100%);
        // background: linear-gradient(top, #222 0%, #45484d 100%);
        border-radius: 20%;
        &:hover {
          transform: scale(1.2);
          // background: linear-gradient(to top, rgb(96, 105, 77) 0% , #385e38 100%);
          background: linear-gradient(to top, #a5a5a5 0%, #e6e0a8 100%);
        }
       }
    }

    
      .squaredTwo label:after { 
        filter: alpha(opacity=0);
        opacity: 0;
        content: '';
        position: absolute;
        width: 7px;
        height: 6px;
        background: transparent;
        top: 4px;
        left: 5px;
        border: 3px solid #fcfff4;
        border-top: none;
        border-right: none;
    
        -webkit-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -o-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        transform: rotate(-45deg);
      }
      
      .squaredTwo label:hover::after {
           filter: alpha(opacity=30);
          opacity: 0.3;
      }
      
      .squaredTwo input[type=checkbox]:checked + label:after {
          filter: alpha(opacity=100);
          opacity: 1;
      }
  }
  
}

.tab-summ {
  margin-top: 4px; 
  border: 1px solid rgba(48, 46, 46, 0.153);
  align-self: flex-end;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 1px 5px;
  border-radius: 4px;
  .line-block_value {
    min-width: 133px;
    text-align: left;
    border-left: 1px solid #00000012;
    padding-left: 8px;
  }
}